import { useCallback, useContext } from "react";
import { LangContext, LangType } from "../utils/LangContext";

interface AvailableProps {
  id: LangType;
  title: string;
}

const availableLangs: AvailableProps[] = [
  { id: "es", title: "Espa침ol" },
  { id: "en", title: "English" },
  { id: "de", title: "Deutsche" },
];

const dev = "Juli치n Lionti";
const commons = {
  developed: {
    es: `Desarrollado por ${dev} 九껋릞`,
    en: `Developed by ${dev} 九껋릞`,
    de: `Entwickelt von ${dev} 九껋릞`,
  },
  title: { es: "Desaf칤o Front", en: "Front Challenge", de: "Herausforderung" },
  description: { es: "Descripci칩n", en: "Description", de: "Beschreibung" },
  ability: { es: "Habilidades", en: "Abilities", de: "Kompetenzen" },
  height: { es: "Altura", en: "Height", de: "H칬he" },
  weight: { es: "Peso", en: "Weight", de: "Gewicht" },
  experience: { es: "Experiencia", en: "Experience", de: "Erfahrung" },
  total_moves: {
    es: "Movimientos totales",
    en: "Total moves",
    de: "Gesamtbewegungen",
  },
};

export default () => {
  const [translation, setTranslation] = useContext(LangContext);

  const setLang = useCallback(
    (lang: LangType) => {
      setTranslation((act) => ({ ...act, lang }));
      localStorage.setItem("lang", lang);
    },
    [setTranslation]
  );

  return { setLang, lang: translation.lang, availableLangs, commons };
};
